.head {
    position: relative;
    height: 100vh;
    max-width: 100%;
    
}


.video-wrapper {
    position: absolute;
    display: flex;
    top: 0;
    left: 0;
    right: 0;
    bottom: $column-width * 3;
}

.video {
    width: 100%;
    height: auto;
    object-fit: cover;
}

.title-bloc {
    margin-bottom: $column-width * 3;
    z-index: 1000;
    transition: ease .5s;
}


.very-big-title {
    font-size: 10vw;
    font-family: 'Helvetica Neu';
    margin-bottom: 30px;
    font-weight: 800;
    transition: all ease .5s;
}

.big-title {
    font-size: 5vw;
    font-family: 'Helvetica Neu';
    margin-bottom: 30px;
    font-weight: 800;
    transition: all ease .5s;
}

.medium-title {
    font-size: 3vw;
    font-family: 'Helvetica Neu';
    margin-bottom: 30px;
    font-weight: 800;
    transition: all ease .5s;
    
}

.small-title {
    font-size: 2rem;
    font-family: 'Helvetica Neu';
    margin-bottom: 30px;
    font-weight: 800;
    transition: all ease .5s;
    
}

@media (max-width: $bp-mobile) {
    .video-wrapper {
        bottom: 0;
    }
    .title-bloc {
        margin-bottom: $column-width * 3; 
    }
    .big-title {
        font-size: 10vw;
        
    }
    .medium-title {
        font-size: 6vw; 
    }
}




