.portrait, .square {
    position: relative;
}


.tileimg {
    object-fit: cover;
    width: 100%;
    max-height: 100%;
}

.inner-bottom {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    justify-content: flex-end;
}

.inner-title {
    font-family: 'Helvetica Neu';
    font-weight: 800;
    font-size: 3vw;
}

.full, .mid {
    transition: ease-in-out .5s;
}



@for $i from 1 through 23 {
    
    .col-#{$i} .portrait  {
        height: $i * $column-width * 5 / 4;
    }
    
}


@for $i from 1 through 23 {
    
    .col-#{$i} .square  {
        height: $i * $column-width;
    }
    
}




@media (max-width: $bp-mobile) {

    .inner-title {
        font-family: 'Helvetica Neu';
        font-weight: 800;
        font-size: 10vw;
    }

    

    @for $i from 1 through 23 {
    
        .col-s-#{$i} .portrait  {
            height: $i * $column-width * 5 / 4;
        }
        
    }
    
}

@for $i from 1 through 100 {
    .scale-#{$i} {
        scale: $i * 0.1;
    }
    

}

.stick {
    position: fixed;
    top: 0;

}

.stick2 {
    position: sticky;
    top: 7vh;

}

.paralax {
    height: 86vh ;
}

