
.bg {
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    height: 3 * $column-width;
    
}
.navtop {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1000;
    height: 80px;
}

.transparent {
    background-color: transparent;
}

.white {
    background-color: #ffffff;
}

.red {
    background-color: #e73b29;
}

.green {
    background-color: #6EC1C5;
}

.pink {
    background-color: #CF8794;
}

.blue {
    background-color: #FF9569;
}

.navigation-link {
    font-size: 2rem;
    opacity: 0.5;
}



@media (max-width: $bp-mobile) {
    .video-container {
        bottom: $column-width * 1;
    }
    .title-bloc {
        margin-bottom: $column-width * 8; 
    }
    .big-title {
        font-size: 10vw;
        
    }
    .bg {
        height: 6 * $column-width;
        
    }
}

.none {
    opacity: 1;
}

.trans {
    opacity: 0;
    transition: ease .5s;
}

.load {
    opacity: 1;
    transition: all ease .5s;
}